<script>
export default {
  data: () => ({
    selectedItem: 0,
    skill: false,
    getdata: null,
    nickname:null,
    email:null,
    profilepicture:null, 
  }),
  created() {
    this.getdata_storage();
  },
  methods: {
    getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("user_data"));

      if (data === null) {
        this.getdata = null;
      } else {
        this.nickname = data.nickname;
        this.email = data.email;
        this.profilepicture = data.profilepicture;
      }
    },
  },
};
</script>
